<template>
  <div id="note-editor">
    <textarea
      :value="activeNote.text"
      @input="editNote"
      class="form-control">
    </textarea>
  </div>
</template>

<script>
	import { mapState, mapMutations } from 'vuex';
	import * as Types from '../store/mutation-types';

export default {
		data() {
			return {}
		},
		computed: {
			...mapState(['activeNote'])
		},
		methods: {
			...mapMutations([Types.EDIT_NOTE]),
			editNote(e) {
				this[Types.EDIT_NOTE](e.target.value);
			}
		}
	}
</script>



