<template>
	<div id="toolbar">
		<i @click="addNote" class="glyphicon glyphicon-plus"></i>
		<i @click="toggleFavorite" class="glyphicon glyphicon-star" :class="{starred: activeNote.favorite}"></i>
		<i @click="deleteNote" class="glyphicon glyphicon-remove"></i>
	</div>
</template>

<script>
	import { mapState, mapMutations } from 'vuex';
	import * as Types from '../store/mutation-types'
	export default {
		data() {
			return {}
		},
		computed: {
			...mapState(['activeNote'])
		},
		methods: {
			...mapMutations([Types.ADD_NOTE]),
			...mapMutations([Types.DELETE_NOTE]),
			...mapMutations([Types.TOGGLE_FAVORITE]),
			addNote() {
				this[Types.ADD_NOTE]();
			},
			deleteNote() {
				this[Types.DELETE_NOTE]();
			},
			toggleFavorite(id) {
				this[Types.TOGGLE_FAVORITE](id);
			}
		}
	}
</script>


